<template>
  <div class="header-model">
    <div class="container">
      <i class="el-icon-menu hidden-sm-and-up" @click="toggleMenu"></i>
      <span class="header-title hidden-sm-and-up">Some OF Myself</span>
      <img src="../../assets/images/logo.jpg" class="left-avator"/>
      <div class="right-tab hidden-xs-only">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal">
          <el-menu-item :index="item.index" v-for="(item, index) in menuArr"
                        :key="index">{{item.name}}</el-menu-item>
        </el-menu>
        <div class="line"></div>
      </div>
    </div>
    <div class="menu-background" ref="menuBack" @click="closeMenu">
      <ul ref="menuList">
        <li v-for="(item, index) in menuArr" :key="index">
          <i :class="['iconfont', item.icon]"></i>
          {{item.name}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'header-model',
    data() {
      return {
        activeIndex: "4",
        isPc: false,
        menuArr: [
          {
            name: "朝花夕拾",
            index: "1",
            icon: 'icon-shuqian',
            path: ''
          },{
            name: "四季豆花",
            index: "2",
            icon: 'icon-zhezhi',
            path: ''
          },{
            name: "前端日记",
            icon: 'icon-wanjufengche',
            index: "3",
            path: ''
          },{
            name: "自我介绍",
            index: "4",
            icon: 'icon-wenju',
            path: ''
          }
        ]
      }
    },
    created() {
      if ((navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i))) {
        this.isPc = true
      }
    },
    methods: {
      toggleMenu() {
        this.$refs.menuBack.style.display = 'block'
        setTimeout(function () {
          this.$refs.menuList.style.transform = 'translateX(0px)'
        }.bind(this),50)
      },
      closeMenu(e) {
        this.$refs.menuList.style.transform = 'translateX(-200px)'
        setTimeout(function () {
          this.$refs.menuBack.style.display = 'none'
        }.bind(this),300)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped type="text/scss">
  .header-model {
    height: 60px;
    background-color: white;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%;
      .left-avator {
        width: 45px;
        height: 45px;
        border-radius: 45px;
        display: inline-block;
      }
      .el-icon-menu{
        font-size: 20px
      }
      .header-title{
        font-size: 16px;
        font-weight: bold;
      }
      .right-tab {
        height: 100%;
        .el-menu {
          height: 100%;
          li {
            height: 100%;
            line-height: 60px;
            font-size: 14px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .menu-background{
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.5);
    ul{
      height: 100%;
      width: 200px;
      overflow-y: auto;
      display: inline-block;
      background-color: white;
      transform: translateX(-200px);
      transition: transform 0.5s;
      li{
        border-bottom: 1px solid #c8d0dc;
        padding-left: 15px;
        box-sizing: border-box;
        line-height: 45px;
        height: 45px;
        font-size: 14px;
        i{
          margin-right: 10px;
        }
      }
    }
  }




  @media only screen and (max-width: 768px) {
    .container {
      padding: 0 15px;
    }
  }
</style>



